<template>
  <div id="app">
    <div style='width:280px;'>
    <vue-ztree :list.sync='listData' :func='nodeClick' :expand='expandClick'>
    </vue-ztree>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // ztreeDataSource:[{
      //           iconClass:"icon-file-normal",
      //           id:1,
      //           name:"音乐",
      //           children:[],
      // },{
      //     id:2,
      //     name:"视频",
      //     iconClass:"icon-file-normal",
      //     children:[{
      //        id:3,
      //        name:"电影",
      //        iconClass:"icon-file-normal",
      //        children:[{
      //           id:4,
      //           name:"国产电影",
      //           url:"",
      //           iconClass:"icon-file-text",
      //        },{
      //           id:5,
      //           name:"好莱坞电影",
      //           url:"",
      //           iconClass:"icon-file-text",
      //        },{
      //           id:6,
      //           name:"小语种电影",
      //           url:"",
      //           iconClass:"icon-file-text",
      //        }]
      //       },{
      //        id:7,
      //        name:"短片",
      //        iconClass:"icon-file-normal",
      //        children:[{
      //           id:9,
      //           name:"电视剧",
      //           url:"",
      //           iconClass:"icon-file-text",
      //        },{
      //           id:10,
      //           name:"短片",
      //           url:"",
      //           iconClass:"icon-file-text",
      //        }]
      //     }]
      // }]
    }
  },
    methods:{
        // 点击节点
        nodeClick:function(m){
            if(m.iconClass!='icon-file-text'){
                if( m.iconClass == 'icon-file-normal'){
                    m.iconClass = 'icon-file-open';
                }else{
                    m.iconClass = 'icon-file-normal';
                }
            }
            this.$emit("getTreeId", m);
        },
        expandClick:function(m){
            if(m.iconClass!='icon-file-text'){
                if( m.iconClass == 'icon-file-normal'){
                    m.iconClass = 'icon-file-open';
                }else{
                    m.iconClass = 'icon-file-normal';
                }
            }
            m.isFolder = !m.isFolder; 
        }
    },
    props: [
        'listData'
    ],
}
</script>

<style scope>
div.ztree_content_wrap {
    height:380px
}
div.ztree_content_wrap div.left {
    float:left;
    width:100%
}
div.zTreeDemoBackground {
    height:500px;
    text-align:left;
    width:100%
}
.expendIcon {
    background-attachment:scroll;
    background-color:transparent;
    background-position:-74px -36px;
    background-repeat:no-repeat;
    border:0;
    cursor:pointer;
    display:inline-block;
    height:16px;
    line-height:0;
    margin:0;
    outline:none;
    position:absolute;
    top:4px;
    vertical-align:middle;
    width:16px
}
ul.ztree {
    background:#fff;
    border:1px solid #ddd;
    height:auto;
    overflow-x:auto;
    overflow-y:scroll;
    width:100%;
    height: 100%;
}
.ztree * {
    font-family:Verdana,Arial,Helvetica,AppleGothic,sans-serif;
    font-size:15px;
    margin:0;
    padding:0
}
.ztree {
    color:#333;
    margin:0;
    padding:5px
}
.ztree li {
    line-height:24px;
    list-style:none;
    margin:0;
    outline:0;
    padding:0;
    position:relative;
    text-align:left;
    white-space:nowrap
}
.ztree li ul {
    margin:0;
    padding:0 0 0 18px
}
.ztree li ul.line {
    background:url("data:image/gif;base64,R0lGODlhCQACAIAAAMzMzP///yH5BAEAAAEALAAAAAAJAAIAAAIEjI9pUAA7") 0 0 repeat-y
}
.ztree li a {
    background-color:transparent;
    color:#333;
    cursor:pointer;
    display:inline-block;
    height:22px;
    margin:0;
    padding:1px 3px 0 5px;
    text-decoration:none;
    vertical-align:top
}
.ztree li a:hover {
    color:#00f;
    text-decoration:none
}
.ztree li a>span.curSelectedNode {
    background:#F1F3FD;
    color:#576FAE;
    height:18px;
    opacity:.8;
    padding:3px 5px
}
.ztree li a.curSelectedNode_Edit {
    background-color:#ffe6b0;
    border:1px solid #ffb951;
    color:#000;
    height:16px;
    opacity:.8;
    padding-top:0
}
.ztree li a.tmpTargetNode_inner {
    background-color:#316ac5;
    border:1px solid #316ac5;
    color:#fff;
    filter:alpha(opacity=80);
    height:16px;
    opacity:.8;
    padding-top:0
}
.ztree li a input.rename {
    border:1px solid #7ec4cc;
    *border:0;
    font-size:12px;
    height:14px;
    margin:0;
    padding:0;
    width:80px
}
.ztree li span {
    display:inline-block;
    line-height:16px;
    margin-right:2px;
    top:3px
}
.ztree li span.button {
    background-attachment:scroll;
    background-color:transparent;
    background-image:url("data:image/png;base64,");
    background-repeat:no-repeat;
    border:0;
    cursor:pointer;
    display:inline-block;
    height:16px;
    line-height:0;
    margin:0;
    outline:none;
    vertical-align:middle;
    width:16px
}
.ztree li span.button.chk {
    cursor:pointer;
    height:13px;
    margin:0 3px 0 0;
    width:13px
}
.ztree li span.button.chk.checkbox_false_full {
    background-position:0 0
}
.ztree li span.button.chk.checkbox_false_full_focus {
    background-position:0 -14px
}
.ztree li span.button.chk.checkbox_false_part {
    background-position:0 -28px
}
.ztree li span.button.chk.checkbox_false_part_focus {
    background-position:0 -42px
}
.ztree li span.button.chk.checkbox_false_disable {
    background-position:0 -56px
}
.ztree li span.button.chk.checkbox_true_full {
    background-position:-14px 0
}
.ztree li span.button.chk.checkbox_true_full_focus {
    background-position:-14px -14px
}
.ztree li span.button.chk.checkbox_true_part {
    background-position:-14px -28px
}
.ztree li span.button.chk.checkbox_true_part_focus {
    background-position:-14px -42px
}
.ztree li span.button.chk.checkbox_true_disable {
    background-position:-14px -56px
}
.ztree li span.button.chk.radio_false_full {
    background-position:-28px 0
}
.ztree li span.button.chk.radio_false_full_focus {
    background-position:-28px -14px
}
.ztree li span.button.chk.radio_false_part {
    background-position:-28px -28px
}
.ztree li span.button.chk.radio_false_part_focus {
    background-position:-28px -42px
}
.ztree li span.button.chk.radio_false_disable {
    background-position:-28px -56px
}
.ztree li span.button.chk.radio_true_full {
    background-position:-42px 0
}
.ztree li span.button.chk.radio_true_full_focus {
    background-position:-42px -14px
}
.ztree li span.button.chk.radio_true_part {
    background-position:-42px -28px
}
.ztree li span.button.chk.radio_true_part_focus {
    background-position:-42px -42px
}
.ztree li span.button.chk.radio_true_disable {
    background-position:-42px -56px
}
.ztree li span.button.switch {
    height:18px;
    width:18px
}
.ztree li span.button.root_open {
    background-position:-92px -54px
}
.ztree li span.button.root_close {
    background-position:-74px -54px
}
.ztree li span.button.roots_open {
    background-position:-92px 0
}
.ztree li span.button.roots_close {
    background-position:-74px 0
}
.ztree li span.button.center_open {
    background-position:-92px -18px
}
.ztree li span.button.center_close {
    background-position:-74px -18px
}
.ztree li span.button.bottom_open {
    background-position:-92px -36px
}
.ztree li span.button.bottom_close {
    background-position:-74px -36px
}
.ztree li span.button.noline_open {
    background-position:-92px -72px
}
.ztree li span.button.noline_close {
    background-position:-74px -72px
}
.ztree li span.button.root_docu {
    background:none
}
.ztree li span.button.roots_docu {
    background-position:-56px 0
}
.ztree li span.button.center_docu {
    background-position:-56px -18px
}
.ztree li span.button.bottom_docu {
    background-position:-56px -36px
}
.ztree li span.button.noline_docu {
    background:none
}
.ztree li span.button.ico_open {
    background-position:-110px -16px;
    margin-right:2px;
    vertical-align:top;
    *vertical-align:middle
}
.ztree li span.button.ico_close {
    background-position:-110px 0;
    margin-right:2px;
    vertical-align:top;
    *vertical-align:middle
}
.ztree li span.button.ico_docu {
    background-position:-110px -32px;
    margin-right:2px;
    vertical-align:top;
    *vertical-align:middle
}
.ztree li span.button.add {
    background-position:-143px 0;
    margin:4px 2px 0 0;
    vertical-align:top;
    *vertical-align:middle;
    display: none !important;
}
.ztree li span.button.edit {
    background-position:-110px -48px;
    margin-right:2px;
    vertical-align:top;
    *vertical-align:middle;
    display: none !important;
}
.ztree li span.button.remove {
    background-position:-110px -64px;
    margin:4px 2px 0 0;
    vertical-align:top;
    *vertical-align:middle;
    display: none !important;
}
.ztree li span.button.up {
    display: none !important; 
    background:url(data:image/png;
    base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAe0lEQVQ4T2NkoBAwUqifgaYGKDAwMKyHutCRgYHhAzbX4nKBAQMDw34GBgYBqCaQZpAhF9ANwWZAAgMDw3wstoEMKWRgYFiALIfNAJBCfhyB+xHJVWAl+ALxP5ohWNWOGoA/EEFxrg8NyIsMDAygtIEBaJqUicpnFLsAAPsjERHQK2WXAAAAAElFTkSuQmCC)
}
.ztree li span.button.down {
    display: none !important; 
    background:url(data:image/png;
    base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAmklEQVQ4T+2TsQ0CMQxF7ToFsEF6WwI2uFEYgVFuBEZhhBR2nxGOIrWRu4gkCCm6DrfOf3qJHYTJwsk87AcgooSIZzc0s6Sq157t0ICZrQ6ISPfsHwDtGJl5A4DDYD82ETnVveYRieiGiGsH8jKzu6o+vgK8SUQXRHxWEA8vqpo+zYZjjDEeQwgOgVLKknP2qzW13yr/+smmDd6ImjsRbQJ62AAAAABJRU5Erk)
}
.zTreeMask {
    background-color:#cfcfcf;
    filter:alpha(opacity=0);
    opacity:0;
    position:absolute;
    z-index:10000
}
.loadSyncNode {
    background-image:url("data:image/gif;base64,");
    display:inline-block;
    height:16px;
    position:relative;
    width:16px
}
.ztree .icon-file-normal,.ztree .icon-file-text,.ztree .icon-file-open{
    color: #6585DF;
}
</style>



